.rc-camera {
  font-size: 12px;
  font-family: 'Arial';
  padding: 0;
  position: relative;
  max-width: 1000px;
  margin: 0 auto;
}
.rc-camera-container {
  width: 100%;
  height: 100%;
  position: relative;
}
.rc-camera-target {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  overflow: hidden;
}
.rc-camera-target-stage {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
.rc-camera-target-img {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.rc-camera-target-content-wrap {
  position: absolute;
  bottom: 0;
  width: 100%;
  z-index: 1999;
  background: rgba(0, 0, 0, 0.8);
  color: #fff;
  padding: 10px 20px 20px;
  transition-property: opacity, transform;
  transition-duration: 1s;
  word-break: break-word;
  font-size: 18px;
  line-height: 26px;
}
.rc-camera-target-current {
  z-index: 99;
}
.rc-camera-button-container {
  box-sizing: border-box;
  width: 100%;
  height: 40px;
  position: absolute;
  top: calc(50% - 20px);
  left: 0;
  z-index: 100;
  display: flex;
  justify-content: space-between;
  padding: 0 20px;
  transition: opacity 0.3s;
}
.rc-camera-button {
  height: 40px;
  cursor: pointer;
}
.rc-camera-pagination {
  position: absolute;
  width: 100%;
  bottom: 10px;
  z-index: 9999;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.rc-camera-pagination-item {
  width: 8px;
  height: 6px;
  border-radius: 3px;
  background: #fff;
  margin: 0 4px;
  transition: all 0.5s;
  padding: 0;
  cursor: pointer;
  position: relative;
}
.rc-camera-pagination-current {
  width: 16px;
  background: red;
}
.rc-camera-pagination-thumbnail {
  position: absolute;
  left: -44px;
  bottom: 10px;
  transition: opacity 0.5s;
}
.rc-camera-show {
  opacity: 1;
}
.rc-camera-hidden {
  opacity: 0;
}



